$web-font-path: "https://fonts.googleapis.com/css?family=PT+Sans:300,400,700,400italic" !default;
$font-family-sans-serif:  "PT Sans", "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif !default;
$font-family-monospace: "Ubuntu Mono", Menlo, Monaco, Consolas, "Courier New", monospace !default;

@import "lumen/variables";
@import "bootstrap";
@import "lumen/bootswatch";
@import "flag-icon";

h1 {
  font-size: 18px;
  margin-top: 10px;
  font-weight: bold;
  display: inline-block;
  margin-right: 2px;

  img {
    vertical-align: baseline; } }

form {
  margin: 10px 0; }

.results {
  margin-top: 130px; }

ul {
  margin: 0;
  padding: 0;
  li {
    margin: 0;
    list-style-type: none; } }

.dns-record {
  font-size: 18px;
  font-family: $font-family-monospace;

  div {
    cursor: pointer;
    &:hover {
      font-weight: bold; } } }

.server__flag {
  display: inline-block;
  margin-right: 5px;
  box-shadow: 0 0 3px #999;

  span {
    display: block; } }

.server__country {
  display: inline-block;
  font-weight: bold; }

footer.main {
  text-align: center; }

progress[value] {
  appearance: none;
  display: block;
  width: 100%;
  height: 4px;
  position: absolute;
  left: 0;
  bottom: -4px;
  border: none;
  opacity: 1;
  transition: opacity 750ms; }

progress::-moz-progress-bar {
  background: #127ba3; }

progress::-webkit-progress-value {
  background: #127ba3; }

progress[value="0"] {
  opacity: 1;
  transition: none; }

progress[value="100"] {
  opacity: 0; }

.g-recaptcha {
  margin-bottom: 15px;
  > div {
    margin: 0 auto; } }
